import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';



const styles = {
  card: {
    height:'100%',
    width:'100%',
  },
  media: {
    height: '250px',
    width:'auto',
  },
  smMainSection: {
    height:'50vmin',
    width:'auto',
  },
};


class ServiceModalOne extends React.Component {
    handleClose = () => {
        this.props.onClose(this.props.selectedValue);
      };
    
      handleListItemClick = value => {
        this.props.onClose(value);
      };
    
    render() {
        const { classes, onClose, selectedValue, ...other } = this.props;
    
        return (
          <Dialog onClose={this.handleClose} aria-labelledby="simple-dialog-title" {...other}>
            <Card className={classes.card}>
              <CardActionArea>
                  <CardMedia
                    className={classes.media}
                    image="https://res.cloudinary.com/ddsihrmda/image/upload/v1548226401/shutterstock_tjrhg5.jpg"
                    title="Contemplative Reptile"
                  />
                <CardContent className={classes.smMainSection}>
                  <Typography gutterBottom variant="h5" component="h2">
                    Lizard
                  </Typography>
                  <Typography component="p">
                    Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging
                    across all continents except Antarctica
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <Button size="small" color="primary">
                  Share
                </Button>
                <Button size="small" color="primary">
                  Learn More
                </Button>
              </CardActions>
            </Card>
          </Dialog>
        );
    }
}

ServiceModalOne.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
  export default withStyles(styles)(ServiceModalOne);